@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{background-image:url(/jweather/static/media/background-2k.7a8a3c1f.jpg);background-size:cover;background-repeat:none}.content-wrapper{font-family:"Open Sans",sans-serif;height:calc(100vh - 60px);width:100vw;justify-content:center;align-content:center}.main-container{display:flexbox;height:400px;width:700px;border-radius:10px 10px 10px 10px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background-color:rgba(255,255,255,0.08);-webkit-backdrop-filter:blur(12px);backdrop-filter:blur(12px);box-shadow:5px 5px 3px rgba(146,145,145,0.166)}.container-up{background-color:rgba(128,128,128,0.39);height:180px;width:600px;position:relative;top:18%;left:50%;display:flex;transform:translate(-50%, -18%);border-radius:8px 8px 0 0}.col-left{position:relative;display:flex;flex-direction:column;left:10%;width:50%;justify-content:center}.temp{color:white;font-size:3rem;width:20%;font-weight:600}.location{width:80%;color:white;font-size:2rem}.right-col{height:130px;width:130px;position:absolute;right:5%;top:18%}.right-col p{color:white;text-align:center}.right-col p::first-letter{text-transform:capitalize}.weather-img{display:flex;justify-content:center}.container-bottom{background-color:rgba(128,128,128,0.39);height:120px;width:600px;position:relative;bottom:4%;left:50%;transform:translate(-50%, 50%);display:flex;justify-content:space-around;align-items:center;border-radius:0 0 8px 8px}.data-item{text-align:center}.data-item h1{color:rgba(0,0,0,0.623);font-size:1rem;font-weight:600;margin-top:10px}.data-item p{font-size:0.9rem;color:white}.form-container{position:absolute;left:50%;bottom:15%;transform:translateX(-50%);display:flex}.search-error-ERROR{color:red;position:absolute;left:50%;bottom:12%;font-size:.85rem;transform:translateX(-50%)}.search-error-OK{display:none}.searchbox{position:relative;width:220px;height:30px;border-style:none;background:none;border-bottom:1px solid #fff;padding-left:5px;color:rgba(255,255,255,0.671);outline:none;letter-spacing:1px;font-style:italic;font-size:12px}.btn{background-color:transparent;border:none}#search-icon{position:relative;background-color:transparent;height:20px;bottom:3px;position:absolute;cursor:pointer}@media (max-width: 1024px){body{background-image:url(/jweather/static/media/background-mobile.ea445b77.jpg);background-size:cover;background-repeat:none}body h1{font-size:1rem}.main-container{width:500px}.container-up{width:400px}.container-bottom{width:400px}}@media (max-width: 560px){body{background-image:url(/jweather/static/media/background-mobile.ea445b77.jpg);background-size:cover;background-repeat:none}.main-container{width:300px;height:290px}.location{font-size:0.9rem}.right-col{width:100px;height:100px}.right-col p{font-size:0.85rem;text-align:center}.img-desc{position:relative;height:80%;width:80%}.container-up{width:280px;height:140px}.container-bottom{width:280px;height:80px;top:0px}.container-bottom h1{font-size:0.8rem}.searchbox{bottom:10%;width:180px}.btn{background-color:transparent;border:none}#search-icon{background-color:transparent;height:20px;position:absolute;bottom:10%;cursor:pointer}.form-container{bottom:80px}.search-error-ERROR{bottom:7%}input[type='text']{font-size:16px}}

*{margin:0;padding:0}.navbar{background:linear-gradient(to right, #000, #001027);opacity:0.85;width:100vw;height:60px;padding-left:20px;display:flex;justify-content:space-between}.logo{color:white;position:relative;font-weight:400;align-self:center;font-size:1.2rem}.hamburger-icon{height:28px;width:28px;position:relative;display:flex;flex-direction:column;justify-content:space-around;top:50%;right:1%;transform:translateY(-50%)}.hamb-line1,.hamb-line2,.hamb-line3{height:3px;width:100%;position:relative;background-color:white;transition:0.5s ease-out}.hamb-line-open1,.hamb-line-open3{height:3px;width:60%;background-color:white;transform:translateY(3px) rotate(40deg);transition:0.5s ease-out}.hamb-line-open2{opacity:0;transition:0.5s ease-out}.hamb-line-open3{transform:translateY(-3px) rotate(-40deg)}@media (max-width: 560px){.logo{font-size:1.1rem}.hamburger-icon{right:5%}}

.menu-open{right:0;top:65px;display:flex;flex-direction:row;position:absolute;width:15%;height:8%;background:linear-gradient(to right, #000, #001027);opacity:0.85;box-shadow:5px 5px 3px rgba(119,119,119,0.39);color:white;justify-content:center;align-items:center;border-radius:10px 0px 0px 10px;z-index:23;transition:0.5s ease-in-out}.menu-closed{display:none}.menu-item{font-family:'Open Sans', sans-serif;font-weight:600;font-size:1.5rem;margin:12%;opacity:0.25;cursor:pointer}.menu-item-active{font-family:'Open Sans', sans-serif;font-weight:600;font-size:1.5rem;margin:12%;pointer-events:none}@media (max-width: 1024px){.menu-open{width:30%;height:8%}.menu-item .menu-item-active{font-size:1rem}}@media (max-width: 560px){.menu-open{width:35%;height:8%}.menu-item .menu-item-active{font-size:1rem}}

